import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'wizard-address-verification',
  templateUrl: './address-verification.component.html'
})
export class AddressVerificationComponent implements OnInit {
  @Output() selectedAddress: EventEmitter<object> = new EventEmitter();

  results: any[] = [];

  constructor(public bsModalRef: BsModalRef) { }

  ngOnInit() {
  }

  public handleAddressSelection(address: object): void {
    this.selectedAddress.emit(address);
    this.bsModalRef.hide();
  }

}
