import { Component, Input } from '@angular/core';
import { FormGroup, AbstractControl } from '@angular/forms';

import { Question } from './question';
import { WebStorageService } from 'src/app/shared/web-storage.service';

@Component({
  selector: 'wizard-question',
  templateUrl: './question.component.html'
})
export class QuestionComponent {
  @Input() question: Question<any>;
  @Input() form: FormGroup;
  @Input() formId: string;

  constructor(private webStorageService: WebStorageService) { }

  private saveToWebStorage(): void {
    this.webStorageService.saveFormToStorage(this.formId, this.form.value);
  }

  get isValid(): boolean {
    return this.form.controls[this.question.id].valid;
  }

  get control(): AbstractControl {
    return this.form.get(this.question.id);
  }

}
