import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit, 
  Output,
  SimpleChanges
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { Question } from '../question';

@Component({
  selector: 'wizard-step',
  templateUrl: './step.component.html'
})
export class StepComponent implements OnInit, OnChanges, OnDestroy {
  @Input() heading: string;
  @Input() subheading: string;
  @Input() form: FormGroup;
  @Input() formId: string;
  @Input() questions: Question<any>;
  @Input() skipToSummary: boolean = false;
  @Output() summary = new EventEmitter<any>();

  ngUnsubscribe: Subject<any> = new Subject();
  showSummaryButton: boolean = false;

  constructor() { }

  ngOnInit() {
    this.form.valueChanges
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(() => {
        // Update summary button display when form values change
        this.showSummaryButton = this.skipToSummary && this.form.valid;
      });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.skipToSummary) {
      // Update summary button display when input from parent changes
      this.showSummaryButton = changes.skipToSummary.currentValue && this.form.valid;
    }
  }

  ngOnDestroy() {
    // Subscription clean up
    // See https://stackoverflow.com/a/41177163
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  private goToSummary(): void {
    this.summary.emit();
  }

}
