import { QuestionValidation, SameAsQuestion, QuestionOptions } from './';

export class Question<T> {
  value?: T;
  id: string;
  label?: string;
  order?: number;
  controlType: string;
  validation?: QuestionValidation;
  type?: string;
  placeholder?: string;
  columns?: number;
  options?: QuestionOptions[];
  dependencies?: string[];
  customStyle?: string;
  sameAs?: SameAsQuestion;

  constructor(options: {
      value?: T,
      id?: string,
      label?: string,
      order?: number,
      controlType?: string,
      validation?: QuestionValidation,
      columns?: number
    } = {}) {
    this.value = options.value;
    this.id = options.id || '';
    this.label = options.label || '';
    this.order = options.order === undefined ? 1 : options.order;
    this.controlType = options.controlType || '';
    this.validation = options.validation || {};
    this.columns = options.columns || 0;
  }
}