import {
  FormControl,
  FormGroup,
  ValidatorFn,
  Validators
} from '@angular/forms';

import {
  Question,
  TextboxQuestion,
  TextareaQuestion,
  CheckboxQuestion,
  RadioQuestion,
  DropdownQuestion,
  DatepickerQuestion
} from './question';

export function toFormQuestion(questions: any[]): Question<any>[] {
  let group: Question<any>[] = [];

  questions.forEach(question => {
    switch (question.controlType) {
      case 'textbox':
        group.push(
          new TextboxQuestion({
            id: question.id,
            value: question.value,
            label: question.label,
            placeholder: question.placeholder,
            order: question.order,
            type: question.type,
            validation: question.validation,
            columns: question.columns
          })
        );
        break;
      case 'textarea':
        group.push(
          new TextareaQuestion({
            id: question.id,
            value: question.value,
            label: question.label,
            placeholder: question.placeholder,
            order: question.order,
            rows: question.rows,
            validation: question.validation,
            columns: question.columns
          })
        );
        break;
      case 'checkbox':
        group.push(
          new CheckboxQuestion({
            id: question.id,
            value: question.value,
            label: question.label,
            placeholder: question.placeholder,
            order: question.order,
            customStyle: question.customStyle,
            validation: question.validation,
            columns: question.columns
          })
        );
        break;
      case 'radio':
        group.push(
          new RadioQuestion({
            id: question.id,
            value: question.value,
            label: question.label,
            order: question.order,
            customStyle: question.customStyle,
            options: question.options,
            validation: question.validation,
            columns: question.columns
          })
        );
        break;
      case 'dropdown':
        group.push(
          new DropdownQuestion({
            id: question.id,
            value: question.value,
            label: question.label,
            placeholder: question.placeholder,
            order: question.order,
            options: question.options,
            validation: question.validation,
            columns: question.columns
          })
        );
        break;
      case 'datepicker':
        group.push(
          new DatepickerQuestion({
            id: question.id,
            value: question.value,
            label: question.label,
            placeholder: question.placeholder,
            order: question.order,
            validation: question.validation,
            columns: question.columns
          })
        );
        break;
    }
  });

  return group.sort((a, b) => a.order - b.order);
}

export function toFormGroup(questions: Question<any>[]): FormGroup {
  let group: any = {};

  

  questions.forEach(question => {
    let validationObj: object = question.validation;
    let validators: ValidatorFn[] = [];
    
    // Build array of validators to pass in the new form control
    // Check to see if validation object from config is empty
    if (Object.keys(validationObj).length !== 0 && validationObj.constructor === Object) {
      // Loop through validation object for each question
      // and push angular validator into the array
      Object.keys(validationObj).forEach(key => {
        switch (key) {
          case 'required':
            validators.push(Validators.required);
          break;
        }
      });
    }

    group[question.id] = new FormControl(question.value, validators);
    
  });

  return new FormGroup(group);
}