import { Inject, Injectable } from '@angular/core';

import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';

const WIZARD_FORM_KEY = 'wizard-form-data';
const WIZARD_STEP_KEY = 'wizard-step-data';

@Injectable({
  providedIn: 'root'
})
export class WebStorageService {

  constructor(@Inject(SESSION_STORAGE) private storage: StorageService) { }
  
  public saveQuestionToStorage(
    formId: string,
    questionId: string,
    questionValue: string | boolean
  ): void {
    // Get all saved form data from storage or set as an empty object
    let allForms = this.storage.get(WIZARD_FORM_KEY) || {};
    // Add form data to object
    allForms[formId] = Object.assign(allForms[formId] || {}, { [questionId]: questionValue });
    // Save data to storage
    this.storage.set(WIZARD_FORM_KEY, allForms);
  }

  public saveFormToStorage(formId: string, formValue: object): void {
    let allForms = this.storage.get(WIZARD_FORM_KEY) || {};
    allForms[formId] = Object.assign(allForms[formId] || {}, formValue);
    this.storage.set(WIZARD_FORM_KEY, allForms);
  }

  public getAllForms(): object {
    const allForms = this.storage.get(WIZARD_FORM_KEY) || {};
    return allForms;
  }

  public getSingleForm(formId: string): object {
    const allForms = this.storage.get(WIZARD_FORM_KEY) || {};
    return allForms[formId] || null;
  }

  public getSingleQuestion(formId: string, questionId: string): object {
    let allForms = this.storage.get(WIZARD_FORM_KEY) || {};
    let value = (allForms[formId] || {})[questionId];
    return value || null;
  }

  public saveStepToStorage(stepNumber: number): void {
    this.storage.set(WIZARD_STEP_KEY, stepNumber);
  }

  public getStepNumber(): number {
    return this.storage.get(WIZARD_STEP_KEY);
  }
}
